.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 2.25rem auto 0 auto;
  align-items: center;
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}

.toggle {
  border: none;
  display: grid;
  grid-template-columns: 1fr 3rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: 0.3rem 0.5rem;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  cursor: pointer;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  grid-row: 1/2;
  justify-self: right;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
}
.toggle label[for=light] {
  grid-column: 3/4;
  grid-row: 1/2;
  justify-self: left;
}
.toggle input[type=radio] {
  opacity: 0;
  cursor: pointer;
  outline: none;
  margin: 0 -2px;
  z-index: -1;
  width: 1.0625rem;
  height: 1.0625rem;
  margin-top: 0.125rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  outline: 1px solid white;
}
.toggle__wrapper {
  height: 1.5rem;
  position: relative;
}
.toggle__background {
  position: absolute;
  top: 0;
  z-index: 100;
  height: 100%;
  width: 100%;
  display: block;
  border-radius: 0.75rem;
  overflow: hidden;
  pointer-events: none;
  background: linear-gradient(255deg, var(--toggle-bg-start) 0%, var(--toggle-bg-end) 98.02%);
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  z-index: 200;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--darg-bg);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  transform: translateX(-50%);
}

.card {
  padding: 1.25rem;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --darg-bg: hsl(230, 17%, 14%);
  --datk-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-bg-top: hsl(225, 100%, 98%);
  --light-bg-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter:"Inter", sans-serif;
  --font-roboto:"Roboto", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--darg-bg);
  color: var(--dark-text2);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}